.homepage {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px 80px;
}

.directory-menu {
  width: 100%;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.menu-list {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 240px;
  flex: 1 1 auto;
  min-width: 30%;
  margin: 10px 7.5px 15px;
  border: 2px solid black;
}
.menu-list:first-child {
  margin-right: 7.5px;
}
.menu-list:last-child {
  margin-left: 7.5px;
}
.menu-list .content {
  height: 90px;
  padding: 0 25px;
  display: flex;
  flex-direction: column;
  border: 2px solid black;
}
.menu-list .title {
  font-weight: bold;
  margin-bottom: 6px;
  font-size: 22px;
  color: lightslategrey;
}
.menu-list .sibtitle {
  font-weight: lighter;
  font-size: 16px;
  text-transform: uppercase;
}