.collectionitem {
  min-width: 23%;
  display: flex;
  flex-direction: column;
  height: 400px;
  align-items: center;
  position: relative;
}

.collectionitem .image {
  width: 100%;
  height: 95%;
  background-size: cover;
  background-position: center;
  margin-bottom: 5px;
  transition: .5s;
}

.collectionitem .collectionfooter {
  width: 100%;
  height: 5%;
  display: flex;
  justify-content: space-between;
  font-size: 18px;
}

.collectionitem .collectionfooter .name {
  width: 90%;
  margin-bottom: 15px;
}

.collectionitem .collectionfooter .price {
  width: 10%;
}

.collectionitem .custom-button {
  position: absolute;
  height: 50px;
  top: 0px;
  display: none;
  transition: .5s;
  width: 80%;
}

.collectionitem .custom-button:hover {
  background-color: white;
  z-index: 3;
}

.collectionitem:hover .custom-button {
  position: absolute;
  display: block;
  top: 300px;
  opacity: 0.7;
  color: white;
  background-color: black;
}

.collectionitem:hover .image {
  background-color: black;
  opacity: .8;
}
