.header {
  display: flex;
  height: 70px;
  width: 100%;
  justify-content: space-between;
  margin-bottom: 25px;
}
.header .logo-container {
  height: 100%;
  width: 70px;
  padding: 25px;
}
.header .options {
  width: 50%;
  height: 100%;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
.header .option {
  padding: 10px 15px;
  text-decoration: none;
  cursor: pointer;
}